import L, { Util, latLngBounds } from 'leaflet';
import { find, isString, parseJSON, isArray, get, isEmpty, map } from '@ttungbmt/utils';
import { Buffer } from 'buffer';
import wkx from 'wkx';
import { featureCollection, feature, center, getGeom, getCoords, getType, circle, flip, bboxPolygon, bbox } from '@turf/turf';

function getFeatureInfoUrl(layer, latlng, params) {
  const map = layer._map;
  const url = layer._url;
  let point = map.latLngToContainerPoint(latlng, map.getZoom()),
      size = map.getSize(),
      bounds = map.getBounds().toBBoxString(),
      wmsParams = layer.wmsParams; // var sw = bounds.getSouthWest(),
  //     ne = bounds.getNorthEast(),
  //     sw = crs.projection._proj.forward([sw.lng, sw.lat]),
  //     ne = crs.projection._proj.forward([ne.lng, ne.lat]),
  //     bbox = [sw.join(','), ne.join(',')].join(',');

  let defaultParams = {
    request: 'GetFeatureInfo',
    service: 'WMS',
    srs: 'EPSG:4326',
    //layer._crs.code,
    transparent: wmsParams.transparent,
    styles: wmsParams.styles,
    version: wmsParams.version,
    //layer._wmsVersion,
    env: wmsParams.env,
    viewparams: wmsParams.viewparams,
    format: layer.options.format,
    bbox: bounds,
    height: size.y,
    width: size.x,
    layers: layer.options.layers,
    query_layers: layer.options.layers,
    info_format: 'application/json'
  };
  params = Util.extend(defaultParams, params || {}); // Remove undefined field from object

  Object.keys(params).forEach(key => params[key] === undefined ? delete params[key] : '');
  params[params.version === '1.3.0' ? 'i' : 'x'] = _.round(point.x); // Fix WMS X and Y incorrectly specified

  params[params.version === '1.3.0' ? 'j' : 'y'] = _.round(point.y);
  return url + Util.getParamString(params, url, true);
}

function getWMSLayers(map) {
  let layer = [];
  map.eachLayer(l => {
    if (l instanceof L.TileLayer.WMS) layer.push(l);
  });
  let timestamp = new Date().getTime(),
      layerOrdered = [];
  Object.keys(layer).sort().forEach(function (key) {
    layerOrdered[key] = layer[key];
    layerOrdered[key]._timestamp = timestamp;
  });
  return layerOrdered;
}

function getWMSLayer(map, predicate) {
  return find(getWMSLayers(map), predicate);
}

const toGeometry = (value, type = 'geojson') => {
  let geometry = null;

  switch (type) {
    case 'ewkb':
    case 'wkb':
      value = Buffer.from(value, 'hex');
      geometry = wkx.Geometry.parse(value);
      break;

    case 'geojson':
      if (isString(value)) value = parseJSON(value);
      geometry = wkx.Geometry.parseGeoJSON(value);
      break;

    default:
      // support type: wkt
      geometry = wkx.Geometry.parse(value);
  }

  return geometry;
};

const toGeoJSON = (value, type) => {
  let geometry = toGeometry(value, type);
  if (!geometry) return null;
  return geometry.toGeoJSON();
};

function toCenter(geom, format = 'latlng') {
  let geometry;

  if (isArray(geom)) {
    geometry = featureCollection(geom.map(g => feature(toGeoJSON(g))));
  } else {
    geometry = toGeoJSON(geom);
  }

  let centerGeo = center(geometry);
  if (format == 'geometry') return getGeom(centerGeo);
  let coords = getCoords(centerGeo);
  return [get(coords, '1', 0), get(coords, '0', 0)];
}

function posToGeoJSON(position) {
  return L.marker(position).toGeoJSON();
}

const pointToCir = (pt, radius = 200) => circle(pt, radius, {
  units: 'meters'
});

const toBounds = items => {
  if (isEmpty(items)) return null;
  let collection = featureCollection(items.map(i => {
    let item = i.type === 'Feature' ? i : feature(i);

    if (getType(item) === 'Point') {
      item = pointToCir(item);
    }

    return item;
  })),
      bounds = latLngBounds(getCoords(flip(bboxPolygon(bbox(collection))))),
      southWest = map(bounds.getSouthWest()),
      northEast = map(bounds.getNorthEast());

  if (JSON.stringify(southWest) === JSON.stringify(northEast)) {
    let item = pointToCir(southWest.reverse());
    return toBounds([item]);
  }

  return [southWest, northEast];
};

export { getFeatureInfoUrl, getWMSLayer, getWMSLayers, posToGeoJSON, toBounds, toCenter, toGeoJSON, toGeometry };
